/** Motif repeats on Overview page */

import { MotifColor, getChartFromMotifs } from "@tanyoknits/shared";
import { leaf, twistL, twistR } from "./stitches";
import { MotifRepeatsProps } from "../../components/MotifRepeatsPattern";

// Gutters
const GUTTER = "p";

const motifColors: MotifColor[] = ["green", "grape", "green"];

export function getMotifRepeats(): MotifRepeatsProps {
  const motifs = [twistL, leaf, twistR].map((m, i) => {
    return { ...m[0].motif, id: motifColors[i] };
  });

  const motifChart = getChartFromMotifs([
    GUTTER,
    motifs[0],
    GUTTER,
    motifs[1],
    GUTTER,
    motifs[2],
    GUTTER,
  ]);

  return {
    chart: motifChart,
    motifList: motifs.slice(0, 2).map((m) => {
      return {
        id: m.id,
        title: m.title,
        times: motifChart.length / m.chart.length,
      };
    }),
  };
}
